




























































































.logo {
  position: relative;
  top: 12px;
  left: 22px;
  display: inline-block;
  width: 88px;
  height: 25px;
  background: url(data:image/png;base64,) no-repeat;
  background-position: left center;
  background-size: contain;
}
.m-logo {
  position: relative;
  width: 180px;
  height: 54px;
  padding: 0;
  font-size: 12px;
  color: #fff;
  line-height: 30px;
  background-color: #5294e0;
}
.sidebar {
  width: 180px;
  position: absolute;
  top: 54px;
  left: 0;
  z-index: 1000;
  display: block;
  padding: 20px;
  overflow-x: hidden;
  overflow-y: auto;
  background-color: #f5f5f5;
  border-right: 1px solid #eee;
  right: 0;
  bottom: 0;
  margin-bottom: 0;
}
.nav-sidebar {
  margin-right: -21px; /* 20px padding + 1px border */
  margin-bottom: 20px;
  margin-left: -20px;
}
.nav-sidebar > li > a {
  padding-right: 20px;
  padding-left: 20px;
}
.nav-sidebar > .active > a,
.nav-sidebar > .active > a:hover,
.nav-sidebar > .active > a:focus {
  color: #fff;
  background-color: #428bca;
}
.main {
  margin-left: 182px;
  margin-top: 54px;
}









































































































